.Error {
  text-align: center;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: url(/static/media/404.9b65855e.png) #6e297b;
}

.Error .content {
  width: 700px;
  max-width: 100%;
}

.Error .content h2 {
  font-size: 18vw;
  letter-spacing: 5px;
  color: #fff;
  font-weight: 900;
